<template>
  <div class="pageContent">
    <el-card class="box-card">
      <el-form :model="ruleForm" stripe fit :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="模板名称：" prop="templateName">
          <el-input style="width:400px;" v-model="ruleForm.templateName"></el-input>
        </el-form-item>
        <el-form-item label="计费方式：">
          <div>按件数</div>
        </el-form-item>
        <el-form-item label="配送区域：">
          <div>
            <el-table fit :data="tableData" style="width: 100%">
              <el-table-column label="可配送区域" min-width="450">
                <template slot-scope="scope">
                  <div class="flex-row-start">
                    <div v-if="scope.row.type">
                      <el-checkbox style="width:450px;" v-model="scope.row.checked">{{scope.row.distributionAreaName}}（未勾选时,则仅下列所选地区可配送）</el-checkbox>
                    </div>
                    <div v-else>{{scope.row.distributionAreaName}}</div>
                  </div>
                </template>
              </el-table-column>
              <el-table-column label="首件（个）" width="200">
                <template slot-scope="scope">
                  <el-input-number 
                    size="mini" 
                    :controls="false"
                    v-model="scope.row.firstPiece" 
                    :disabled="!scope.row.checked" 
                    :min="1"
                    :max="100"
                    :step="1" 
                    step-strictly></el-input-number>
                </template>
              </el-table-column>
              <el-table-column label="运费（元）" width="200">
                <template slot-scope="scope">
                  <el-input-number
                    size="mini" 
                    :controls="false"
                    :disabled="!scope.row.checked" 
                    v-model="scope.row.firstFreightPrice" 
                    :precision="2" 
                    :min="0"
                    :max="1000"
                    :step="0.1"
                    ></el-input-number>
                </template>
              </el-table-column>
              <el-table-column label="续件（个）" width="200">
                <template slot-scope="scope">
                  <el-input-number 
                    size="mini" 
                    :controls="false"
                    v-model="scope.row.addPiece" 
                    :disabled="!scope.row.checked" 
                    :min="1"
                    :max="100"
                    :step="1" 
                    step-strictly></el-input-number>
                </template>
              </el-table-column>
              <el-table-column label="续费（元）" width="200">
                <template slot-scope="scope">
                  <el-input-number 
                    size="mini" 
                    :controls="false"
                    :disabled="!scope.row.checked" 
                    v-model="scope.row.addFreightPrice" 
                    :precision="2" 
                    :min="0"
                    :max="1000"
                    :step="0.1"
                    ></el-input-number>
                </template>
              </el-table-column>
              <el-table-column label="操作" width="200">
                <template slot-scope="scope">
                  <div v-if="!scope.row.handle">-</div>
                  <div v-else>
                    <el-button type="text" @click="showSendFun('edit',scope)">修改</el-button>
                    <el-button type="text" @click="deleteItem(scope)">删除</el-button>
                  </div>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-form-item>
        <el-form-item label="">
          <el-button type="primary" plain @click="showSendFun('new')">指定配送区域和运费</el-button>
        </el-form-item>
        <el-form-item label="">
          <el-button :loading="addLoading" type="primary" @click="subForm('ruleForm')">保 存</el-button>
          <el-button @click="back">返 回</el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <!-- 配送区域弹出框 -->
    <el-dialog 
      title="选择配送区域" 
      :visible.sync="showSend" 
      width="30%"
      :close-on-click-modal='false'
      :close-on-press-escape="false"
      :show-close="false"
      >
      <div>
        <el-tree
          ref="tree"
          :data="treeData"
          show-checkbox
          node-key="code"
          highlight-current
          :props="defaultProps">
        </el-tree>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="closeTree">关 闭</el-button>
        <el-button type="primary" @click="treeSub">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { getFreightSystemDistrictInfo , insertFreightTemplateInfo } from '@/api/logistics/logisticsTemplate'
import { mapState } from 'vuex'
export default {
  data(){
    return{
      showSend:false,                       // 目录树弹出框
      treeData:[],                          // 目录树数据
      defaultProps: {
        children: 'children',
        label: 'name'
      },
      diaIndex:0,
      diaType:'',
      ruleForm:{},
      tableData: [
        {
          firstPiece: '1',              // 首件（个）
          firstFreightPrice:'0.00',     // 首件（元）
          addPiece:'1',                 // 续件（个）
          addFreightPrice:'0.00',       // 续费
          distributionAreaName: '全部地区可配送',
          handle:false,
          checked:true,
          type:true
        }, 
      ],
      rules: {
        templateName: [
          { required: true, message: '请输入模板名称', trigger: 'blur' },
          { min: 1, max: 20, message: '长度在 1 到 20 个字符', trigger: 'blur' }
        ],
      },
      addLoading:false,
    }
  },
  created(){
    this.getTree()
  },
  methods:{
    treeSub(){
      let data = this.$refs.tree.getCheckedNodes();
      data = data.filter((item) => item.disabled == false);
      console.log('data过滤已选中',data)
      if(data.length == 0){
        this.$message.error('请勾选至少一条后重试');
        return;
      }
      let arr1 = [];
      data.forEach(e => {
        e.disabled = true
        if(e.level == 2){
          let b = arr1.find((v) => {
            return v.code == e.parentCode;
          });
          console.log(b)
          if(b){
            b.children.push(e)
          }else{
            let arr = []
            arr.push(e)
            let obj ={
              code:e.parentCode,
              children:arr,
              name:e.parentName
            }
            arr1.push(obj)
          }
        }
      })
      console.log('arr1',arr1)
      let nameStr = []
      arr1.forEach(e=>{
        if(e.children.length == e.children[0].parentLenth){
          nameStr.push(e.name)
        }else{
          e.children.forEach(item => {
            nameStr.push(item.name)
          })
        }
      })
      console.log('nameStr',nameStr)
      nameStr = nameStr.join(' , ')
      console.log('nameStr',nameStr)
      let sheng = [];
      let filterSheng = [];
      // 总数据筛选省
      if(this.diaType == 'edit'){
        this.tableData.splice(this.diaIndex, 1)
        this.diaType = ''
      }
      let obj = {}
      obj.handle = true;
      obj.freightTemplateCityDtoList = data.filter((item) => item.level == 2);
      obj.distributionAreaName = nameStr
      obj.firstPiece = '1'
      obj.firstFreightPrice = '0.00'
      obj.addPiece = '1'
      obj.addFreightPrice = '0.00'
      obj.checked = true
      obj.areaType = 1;
      obj.selectData = data;
      
      this.showSend = false;
      this.tableData.push(obj)
    },
    showSendFun(e,e1){
      console.log('e',e)
      console.log('e1',e1)
      if(e == 'edit'){
        console.log(e1)
        e1.row.selectData.forEach(a=>a.disabled = false)
        this.diaType = 'edit'
        this.diaIndex = e1.$index
        this.diaObj = e1
      }
      this.showSend = true;
    },
    subForm(formName){
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if(!this.tableData[0].checked){
            if(this.tableData.length == 1){
              this.$message.error('请添加可配送的区域');
              return;
            }else{
              let freightTemplateRuleDtoList = [];
              for(var i=1;i<this.tableData.length;i++){
                freightTemplateRuleDtoList.push(this.tableData[i])
              }
              this.ruleForm.freightTemplateRuleDtoList = freightTemplateRuleDtoList
            }
          }else{
            this.tableData[0].areaType = 0
            this.ruleForm.freightTemplateRuleDtoList = this.tableData
          }
          this.ruleForm.shopId = this.shop.shopId
          this.addLoading = true;
          console.log('添加模板请求的数据',this.ruleForm)
          insertFreightTemplateInfo(this.ruleForm).then(res=>{
            console.log('添加模板',res)
            if(res.code == 200){
              this.$message({
                message: '创建成功',
                type: 'success'
              });
              setTimeout(()=>{
                this.$router.replace({path:'/logistics/logisticsTemplate'})
                this.addLoading = false;
              },1000)
            }else{
              this.$message.error(res.message);
              this.addLoading = false;
            }
          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    // 关闭
    closeTree(){
      if(this.diaType == 'edit'){
        console.log('修改了')
        this.diaObj.row.selectData.forEach(e => e.disabled = true)
        this.diaType = ''
      }else{
        console.log('没走')
      }
      this.showSend = false;
    },
    // 删除
    deleteItem(e){
      console.log(e)
      console.log(e.$index)
      e.row.selectData.forEach(e => {
        e.checked = false
        e.disabled = false
      })
      this.tableData.splice(e.$index, 1)
    },
    // 返回
    back(){
      this.$confirm('确定放弃编辑运费模板吗?', '', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$router.replace({path:'/logistics/logisticsTemplate'})
      }).catch(() => {});
    },
    // 获取查询树
    getTree(){
      getFreightSystemDistrictInfo().then(res=>{
        console.log('查询树',res)
        this.treeData = res.data

      })
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>

</style>