import request from '@/utils/request'
// 查询店铺配置信息
export function getShopConfigInfo(id) {
  return request({
    url: `/sys/business/shopInfo/getShopConfigInfo?shopId=${id}`,
    method: 'get',
  })
}
// 更新店铺配置信息
export function updateShopConfigInfo(data) {
  return request({
    url: `/sys/business/shopInfo/updateShopConfigInfo`,
    method: 'post',
    data: data
  })
}
